
import CameraAltIcon from '@material-ui/icons/CameraAlt';
import CodeIcon from '@material-ui/icons/Code';
import MovieIcon from '@material-ui/icons/Movie';
import { useState } from 'react';
import recruit from '../recruit.png';
import social from '../social.png';
import './home.component.scss'


export default function Home() {

    const [deltaY, setDeltaY] = useState(0)
    const [action, setAction] = useState(false)
    const handleWheel = (e) => {
        setDeltaY(e.deltaY);

        if (deltaY === 100) {
            setAction(true);
        }
        if (deltaY === -100) {
            setAction(false);
        }
    }
    return (

        <div className="App" onWheel={(e) => handleWheel(e)}>
            <div class="ins"> Scroll to view more </div>
            <header className="App-header">

                {
                    <p class={`item ${action ? '' : 'active'}`}>
                        <p>
                            <CameraAltIcon className="icon icon-cam"></CameraAltIcon>
                            <CodeIcon className="icon icon-code"></CodeIcon>
                            <MovieIcon className="icon icon-movie"></MovieIcon>
                        </p>
                        <img className="App-logo" src={`https://scontent.fcok7-1.fna.fbcdn.net/v/t1.6435-9/138811275_3326192654157063_3707747376322310859_n.jpg?_nc_cat=102&ccb=1-3&_nc_sid=09cbfe&_nc_ohc=pSJ4DxfTTs0AX9Al48h&tn=gWpHLYFl_lCdY40W&_nc_ht=scontent.fcok7-1.fna&oh=98f14d83dba5e67480befe9f7dd7492f&oe=60D292B0`} alt="Logo" />
                        <h1> Vaisakh M A </h1>
                        <p> <span >Hi, I'm a   </span>

                            <span class="toBeReplaced"> </span>
                        </p>
                    </p>
                }

                {
                   
                    <div  class={`entry-buttons item ${action ? 'active' : ''}`}>

                        <div class="options"> <p> <img alt="recruit" src={recruit}></img> </p>  <p> You're a Recruiter</p></div>
                        <div class="options">   <p> <img alt="social" src={social}></img> </p> <p> You're a Follower</p></div>

                    </div>
                }


            </header>

        </div >
    )
}
